
header{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.App header h1{
  color:#fff;
  letter-spacing: 5px;
  font-size: 3rem;
  margin-top: 5vh;
}
.App header h3{
  color:#fff;
  letter-spacing: 2px;
  font-size: 1.5rem;
}
main{
  display:flex;
  position: absolute;
  left: 0;
  top:50%;
  transform: translateY(-50%);
  
}
.weatherContainer{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color:#fff;
  font-size: 1.9rem;
  width: 20vw;
  flex-shrink: 1;
}
.day{
  font-size: 1.7rem;
}
footer{
  position: absolute;
  left: 0;
  bottom: 0;
  height:5vh;
  width:100vw;
  color:#fff;
  text-align: center;
  font-size: 1.3rem;
}
@media(max-width:415px){
  .App header h1{
    font-size: 2.8rem;
  }
  .App header h3{
    font-size: 1.5rem;
  }
  .weatherContainer{
    font-size: 1.2rem;
  }
  .day{
    font-size: 1.1rem;
  }
  img{
    width:90%;
  }
}
@media(max-width:750px) and (orientation:landscape){
  .App header h1{
    font-size: 2.3rem;
  }
  .App header h3{
    font-size: 1.4rem;
  }
  .weatherContainer{
    font-size: 1.2rem;
  }
  .day{
    font-size: 1.1rem;
  }
  img{
    width:60%;
  }
  main{
    max-height: 50vh;
  }
}
@media(min-height:900px) and (min-width:1600px){
  .App header h1{
    font-size: 3.8rem;
  }
  .App header h3{
    font-size: 2rem;
  }
  .weatherContainer{
    font-size: 2.5rem;
  }
  .day{
    font-size: 2.2rem;
  }
}